const mediaendpoint = "https://evve.herokuapp.com/media";
function Works({ fadeIn }) {
  const [works, setWorks] = useState([]);
  const [loading, setLoading] = useState(false);
  const [expand, setExpand] = useState({ height: "50vh", overflow: "hidden" });

  const [viewall, setViewall] = useSpring(() => ({
    opacity: 1,
    pointerEvents: "all",
  }));

  useEffect(() => {
    setLoading(true);
    axios.get(mediaendpoint + "/works.json").then((w) => {
      setWorks(w.data);
      setLoading(false);
    });
  }, []);

  return (
    <a.div style={{ ...fadeIn, ...expand }} className="works">
      <p className="work-title">My Work</p>
      {loading ? <LinearProgress /> : <></>}
      <div className="work-videos">
        {works.map((w, i) => (
          <Work work={w} index={i} key={i} />
        ))}
      </div>
      {!loading ? (
        <a.div className="shade" style={viewall}>
          <span
            onClick={(e) => {
              // setStyle(openstyle);
              setExpand({ height: "auto", overflow: "auto" });
              setViewall({ opacity: 0, pointerEvents: "none" });
            }}
          >
            view all
          </span>
        </a.div>
      ) : (
        ""
      )}
    </a.div>
  );
}

function Work({ work, index }) {
  const slide = useSpring({
    config: { mass: 1, friction: 1, damping: 1, frequency: 1 },
    from: {
      opacity: 0,
      transform: "translateY(50px) rotate(1deg)",
    },
    opacity: 1,
    transform: "translateY(0) rotate(0)",
    delay: 1500 + 100 * index,
  });

  return (
    <a.div style={slide} className="work">
      <div className="cover-img">
        <img src={mediaendpoint + work.image} alt="" />
      </div>
      <div className="about">
        <span
          className="title"
          onClick={() => {
            work.links &&
              (work.links.default
                ? window.open(work.links.default.link)
                : window.open(work.links.github.link));
          }}
        >
          {work.title}
        </span>
        <span className="description">{work.description}</span>
        <DevStatus phase={work.phase} />
        {work.links ? <MoreInfoLinks links={work.links} /> : ""}
      </div>
    </a.div>
  );
}

function MoreInfoLinks({ links }) {
  links = { ...links };
  delete links.default;
  return (
    <div className="more-info">
      {Object.values(links).map((link, i) =>
        link ? (
          <img
            onClick={() => window.open(link.link)}
            className="link-logo"
            key={i}
            src={link.logo}
            alt=""
            style={{
              filter: link.link.includes("github.com") ? "invert(1)" : "",
            }}
          />
        ) : (
          ""
        )
      )}
    </div>
  );
}

function DevStatus({ phase }) {
  return (
    <div className="dev-status">
      <div className="indicator" style={{ background: phase.color }} />
      <span style={{ color: phase.color }}>{phase.message}</span>
    </div>
  );
}

